/* List table */
.list-table {
  height: auto;
  display: table;
  margin: 0;
  padding: 0;
  > li {
    display: table-cell;
    vertical-align: middle;
    position: relative;
    padding: 0;

    &.search {
      position: static;
    }
  }
}

/* mostly used for nav items */
.disabled:not(.btn),
.disabled:not(.btn) > * {
  filter: grayscale(80%);
  opacity: 0.8;
  cursor: not-allowed;

  ul,
  .collapse-sign {
    display: none;
  }
}

ul.list-verticle {
  margin: 0;
  padding: 0;
  list-style: none;
}

.show {
  & > .dropdown-menu-animated {
    @include transform(scale(1) !important);
    @include transform-origin(29px -50px);
    opacity: 1;
    visibility: visible;
  }
}

hr {
  border: none;
  border-bottom: 1px dashed #eee;

  &.hr-xl {
    margin: 3rem 0;
  }
}

.bg-trans-gradient {
  background: -webkit-linear-gradient(
    250deg,
    desaturate($info-500, 25%),
    desaturate($primary-500, 10%)
  );
  background: linear-gradient(
    250deg,
    desaturate($info-500, 25%),
    desaturate($primary-500, 10%)
  );
}

.bg-brand-gradient {
  @extend %nav-bg;
}

/* custom scroll */
.custom-scroll,
.custom-scrollbar {
  @extend %custom-scroll;
}

/* table no border */
.table-border-0 {
  th,
  td {
    border: 0 !important;
  }
}

/* table calendar */
.table-calendar {
  table-layout: fixed;

  th {
    border: 0px !important;
    font-weight: 500;
  }

  tr {
    td:first-child {
      border-left: 0;
    }
    td:last-child {
      border-right: 0;
      padding-right: 10px;
    }
  }

  td,
  th {
    text-align: right;
    vertical-align: top;
    padding: 5px 8px;
    position: relative;
  }
}

/* list spaced */
.list-spaced {
  li {
    margin-top: 7px;
    margin-bottom: 7px;

    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}
.list-spaced {
  > li {
    padding: 0 0 $spacer * 0.2;
  }
}

/* profile images */
.profile-image {
  width: $profile-image-width;
  height: $profile-image-width;
}

.profile-image-md {
  width: $profile-image-width-md;
  height: $profile-image-width-md;
}

.profile-image-sm {
  width: $profile-image-width-sm;
  height: $profile-image-width-sm;
}

/* image share */
.img-share {
  width: auto;
  height: $image-share-height;
}
span.img-share {
  width: $image-share-height + ($image-share-height * 0.467);
  height: $image-share-height;
}

.notes {
  padding: 5px;
  background: #f9f4b5;
}

