/* disclaimer class */
.disclaimer {
  padding-left: 10px;
  font-size: rem($fs-base - 5);
  color: #a2a2a2;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-style: italic;
}

/* horizontal scrolling */
.scrolling-wrapper {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;

  -webkit-overflow-scrolling: touch;

  .card {
    flex: 0 0 auto;
  }
  &::-webkit-scrollbar {
    display: none;
  }
}

/* online status */
.status {
  position: relative;

  &:before {
    content: ' ';
    position: absolute;
    width: 15px;
    height: 15px;
    display: block;
    top: -2px;
    right: -2px;
    background: $fusion-500;
    border-radius: 50%;
    border: 2px solid #fff;
  }

  &.status-sm:before {
    width: 10px;
    height: 10px;
    border-width: 1px;
    top: 0;
    right: 0;
  }

  &.status-success:before {
    background: $success-500;
  }

  &.status-danger:before {
    background: $danger-500;
  }

  &.status-warning:before {
    background: $warning-500;
  }
}

/* containers */
.container {
  &.container-sm {
    max-width: map-get($grid-breakpoints, sm);
  }

  &.container-md {
    max-width: map-get($grid-breakpoints, md);
  }

  &.container-lg {
    max-width: map-get($grid-breakpoints, lg);
  }
}

/* responsive visibility */
/* https://getbootstrap.com/docs/3.4/css/#responsive-utilities */
@each $bp in map-keys($grid-breakpoints) {
  .hidden-#{$bp}-up {
    @include media-breakpoint-up($bp) {
      display: none !important;
    }
  }
  .hidden-#{$bp}-down {
    @include media-breakpoint-down($bp) {
      display: none !important;
    }
  }
}

/* display frame */
.frame-heading {
  font-size: rem($fs-base);
  margin-bottom: 1rem;
  color: lighten($fusion-50, 7%);
  font-weight: 500;

  small {
    font-size: rem($fs-base);
    margin-bottom: 0.5rem;
  }
}
.frame-wrap {
  background: white;
  padding: 0;
  margin-bottom: 3rem;
}
* > .frame-wrap:last-child {
  margin-bottom: 0 !important;
}

/* time stamp */
.time-stamp {
  font-size: rem($fs-sm);
  margin: $p-1 0 0 0;
  color: $fusion-200;
  font-weight: 300;
}

/* data-hasmore */
[data-hasmore] {
  position: relative;
  color: $white;
  &:before {
    content: attr(data-hasmore);
    border-radius: inherit;
    background: rgba($black, 0.4);
    height: inherit;
    width: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: inherit;
  }
}

/* code */
code {
  background: lighten(desaturate($primary-500, 30%), 65%);
  padding: 4px 7px;
  border-radius: 4px;
}

/* star checkbox */
.star {
  visibility: hidden;
  font-size: 1.5em;
  cursor: pointer;
}
.star:before {
  content: '';
  position: absolute;
  visibility: visible;
}
.star:checked:before {
  content: '';
  position: absolute;
}
